@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Smooch&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Smooch&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body {
  font-family: 'Roboto Condensed', sans-serif;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Roboto', sans-serif;
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: rgb(233, 233, 233);
}


.title{
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.title h6{
    text-transform: capitalize;
    margin: 0 auto;
    color: #444;
    text-align: center;
    font-family: 'Poppins', sans-serif;
}

.title h3{
    color: #222;
    text-transform: capitalize;
    margin: 0 auto;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}

.title .underline{
    width: 45px;
    height: 4px;
    background: forestgreen;
    margin: 0 auto;
    border-radius: 10px;
}

.tour-box{
    width: 450px;
    background: #fff;
    box-shadow: 3px 2px 6px rgba(0,0,0,0.3);
    overflow: hidden;
}

.head{
    position: relative;
}

.btn{
    border: none;
    outline: none;
    background: transparent;
    color: forestgreen;
    text-transform: capitalize;
    font-weight: 800;
    letter-spacing: 1px;
    font-family: 'Poppins', sans-serif;
}

.tour-img{
    width: 100%;
}

.tour-info-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    letter-spacing: 0.5px;
}

.tour-info h4{
    text-transform: uppercase;
}




.country-box h5{
    text-transform: uppercase;
    color: #333;
    letter-spacing: 1px;
}

.country-box .location{
    text-transform: capitalize;
    color: forestgreen;
    font-family: 'Poppins', sans-serif;
}


.overview{
    padding:0.5rem 1rem;
}

.overview p{
    color: #666;
    font-size: 0.8rem;
}

.left-icon{
    color: forestgreen;
    margin-right: 5px;
    font-size: 0.75rem;
}

.right-icon{
    color: forestgreen;
    margin-left: 5px;
    font-size: 0.75rem;
}

.btn-container{
    display: flex;
    align-items: center;
    justify-content: center;
    padding:1rem 0;
}

.next-btn{
    margin-left: 1.5rem;
    display: flex;
    align-items: center;
    border: 2px solid #333;
    color: #333;
    padding: 0.1rem 0.4rem;
    font-size: 12px;
}

.back-btn{
    display: flex;
    align-items: center;
    border: 2px solid #333;
    color: #333;
    padding: 0.1rem 0.4rem;
    font-size: 12px;
}

iframe{
    width: 100%;
    height: 100%;
    border: 0;
}

.map-box{
    position: absolute;
    top: 0;
    left: 100%;
    width: 100%;
    transition: 300ms ease-in-out;
    bottom: 0;
}

.map-box.active{
    left: 0;
}

@media (max-width:800px){
    .tour-box{
        width: 98%;
        margin: 0 auto;
    }

    .tour-info h4{
        font-size: 13px;
    }

    .tour-info h5{
        font-size: 12px;
    }

    .map-btn{
        font-size: 11px;
    }
}
